@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans/OpenSans-Regular.ttf"); }

* {
  font-family: "Open Sans", sans-serif; }

body {
  background: #e6e6e6;
  padding: 0 10vw;
  height: 100vh;
  box-sizing: border-box;
  max-width: 100vw; }

.appWrapper, #root {
  width: 100%; }

.appWrapper {
  padding: 4vw;
  display: flex;
  flex-direction: column; }

.addItemWrapper, .searchItemWrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: all;
  transition: all 0.5s ease; }
  .addItemWrapper form, .searchItemWrapper form {
    display: flex;
    flex-direction: column; }
    .addItemWrapper form input, .searchItemWrapper form input {
      width: 20vw;
      padding: 1vw;
      border: 2px solid white;
      color: white;
      background-color: transparent;
      outline: 0;
      border-radius: 2px;
      font-size: 1.2em;
      margin-bottom: 2vw; }
      .addItemWrapper form input:last-child, .searchItemWrapper form input:last-child {
        margin: 0; }
      .addItemWrapper form input:focus, .addItemWrapper form input:active, .searchItemWrapper form input:focus, .searchItemWrapper form input:active {
        border-color: #6fa5fc; }
      .addItemWrapper form input::-webkit-input-placeholder, .searchItemWrapper form input::-webkit-input-placeholder {
        /* Edge */
        color: rgba(255, 255, 255, 0.8); }
      .addItemWrapper form input:-ms-input-placeholder, .searchItemWrapper form input:-ms-input-placeholder {
        /* Internet Explorer 10-11 */
        color: rgba(255, 255, 255, 0.8); }
      .addItemWrapper form input::placeholder, .searchItemWrapper form input::placeholder {
        color: rgba(255, 255, 255, 0.8); }
    .addItemWrapper form button, .searchItemWrapper form button {
      width: 20vw;
      padding: 1vw;
      border: 2px solid white;
      color: white;
      background-color: transparent;
      outline: 0;
      border-radius: 2px;
      font-size: 1.2em;
      cursor: pointer; }
      .addItemWrapper form button:hover, .addItemWrapper form button:focus, .addItemWrapper form button:active, .searchItemWrapper form button:hover, .searchItemWrapper form button:focus, .searchItemWrapper form button:active {
        background-color: white;
        color: black; }

.addItemWrapper--hidden, .searchItemWrapper--hidden {
  opacity: 0;
  pointer-events: none; }
